<template>
	<div class="_page">
		<div class="home1 flex">
			<div
				class="ev"
				v-for="(item, index) in List"
				:key="index"
			>
				<div
					class="bg"
					:style="'background-image: url(' + require(`@/assets/img/home/homebg-${bgList[index] || 3}.jpg`) + ')'"
				></div>
				<div class="intr">
					<h2 @click="itemTo(item)">{{ item.meta.title }}</h2>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import '@/assets/css/home.css';
import { routes } from '@/router/routes';
export default {
	name: 'home',
	data() {
		return {
			List: [],
			bgList: [],
		};
	},
	created() {
		this.List = routes.filter((x) => x.meta.homeShow !== false);
		let bgList = localStorage.getItem('bgList');
		if (bgList) {
			bgList = JSON.parse(bgList);
			if (this.List.length != bgList.length) {
				this.getBgList();
				localStorage.setItem('bgList', JSON.stringify(this.bgList));
				return;
			}
			this.bgList = bgList;
		}
		this.getBgList();
		localStorage.setItem('bgList', JSON.stringify(this.bgList));
	},
	methods: {
		itemTo(item) {
			console.log(item);
			this.$router.push(item.path);
		},
		getBgList() {
			if (this.bgList.length >= this.List.length) {
				return;
			}
			var num = Math.floor(Math.random() * 10) + 1;
			if (!this.bgList.includes(num)) {
				this.bgList.push(num);
			}
			this.getBgList();
		},
	},
};
</script>

<style></style>
